<template>
  <div class="login">
    <div id="poster">
      <h1 class="logo">安徽省标准化信息服务平台</h1>
      <el-form class="login-container" label-position="left" label-width="0px">
        <h3 class="login_title">系统登录</h3>
        <el-form-item>
          <el-input v-model="loginForm.username" placeholder="请输入帐号"></el-input>
        </el-form-item>

        <el-form-item>
          <el-input placeholder="请输入密码" v-model="loginForm.password" @keyup.enter.native="login" show-password></el-input>
        </el-form-item>

        <el-form-item style="width: 100%">
          <el-button type="primary" style="width: 100%;background: #505458;border: none" @click.stop="login">登录</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    login() {
      if (this.loginForm.username === '') return this.$message.warning('请输入帐号')
      if (this.loginForm.password === '') return this.$message.warning('请输入密码')

      const params = { account: this.loginForm.username, password: this.$md5(this.loginForm.password) }
      this.httpPost('/user/login', params).then(ret => {
        this.$sessionData('set', 'USER', ret)
        this.$router.push('/home')
      })
    }
  },
  mounted() {
    // console.log(window.innerHeight)
    // document.getElementById('poster').style.height = window.innerHeight + 'px'
  }
}
</script>

<style lang="scss" scoped>
.login {
  background: url('../assets/images/timg.jpg') no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
}
#poster {
  width: 800px;
  height: 500px;
  text-align: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.logo {
  margin: 0;
  font-size: 36px;
  color: #fff;
  margin-bottom: 50px;
}
.login-container {
  width: 350px;
  height: 260px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 35px 35px 15px 35px;
  background-color: hsla(0, 0%, 100%, 0.9);
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  margin: auto;
}

.login_title {
  margin: 0px auto 20px;
  text-align: center;
  color: #505458;
  font-size: 20px;
}

.el-form-item {
  margin-bottom: 20px;
}

/deep/input {
  height: 40px;
}

.el-button {
  margin-top: 20px;
  height: 40px;
}
/deep/.footer {
  height: auto !important;
  padding-top: 20px;
  text-align: center;
}
</style>
